<script setup lang="ts">
    const tabFlag = ref(0);
    import "vue3-carousel/dist/carousel.css";
    import { Carousel, Slide, Pagination, Navigation } from "vue3-carousel";
    import { computed, ref, watch, watchEffect } from "vue";
    import useFetch from "@/composables/useFetch";
    import useFileUpload from "@/composables/useFileUpload";
    import { mainMapPosList } from "@/util/common";

    // 지역스포츠클럽
    const myCarousel = ref<any>(null);
    const chosenCity = ref<SidoList | null>(null);
    const cities = ref<SidoList[] | null>(null);
    const regionSlideList = ref<OnMainClubResList[]>([]);
    const playState = ref(true);
    const { fileImage } = useFileUpload();

    useFetch<SidoList[]>({
        url: "/search/si-do",
        param: { apmtYn: "N" },
        onSuccess: data => {
            data.shift();
            cities.value = data;
            chosenCity.value = data[0];
        }
    });

    /// 지역별 클럽 조회
    useFetch<MainTabCon01Type>({
        url: "/on-main",
        dependancy: [() => chosenCity.value],
        enable: () => !!chosenCity.value,
        param: () => ({
            apmtYn: "N",
            siDoCd: chosenCity.value?.siDoCd
        }),
        onSuccess: data => {
            regionSlideList.value = data.onMainClubResList;
            myCarousel.value.slideTo(0);
        }
    });

    const regionUrl = computed(() => {
        const targetSlideIdx = myCarousel.value?.data.currentSlide.value;
        const targetSlideData = regionSlideList.value[targetSlideIdx];
        return targetSlideData?.rpsUrl ?? "/";
    });

    const mainMapPos = computed(() => {
        return mainMapPosList.find(item => item.siDoCd === chosenCity.value?.siDoCd)?.posX ?? "0px";
    });
</script>

<template>
    <div class="main-conts main-conts-maps d-flex ai-start jc-between">
        <div class="l">
            <h2>스포츠 <strong>클럽 안내</strong></h2>
            <span class="rgTit mb-19 mt-47"
                ><VIcon icon="ic_location_green" :style="{ color: '#2e7a53' }" /> 경상북도</span
            >
            <div class="left-wrap d-flex">
                <div class="l-slide">
                    <div class="slidewrap">
                        <carousel
                            class="regionSlide"
                            :transition="500"
                            :autoplay="playState ? 2000 : 0"
                            :items-to-show="1"
                            ref="myCarousel"
                            wrapAround
                        >
                            <slide v-for="(slide, index) in regionSlideList" :key="index" class="slide-item">
                                <div class="img-top">
                                    <img
                                        :src="fileImage({ fileUuid: slide.rpsImgFileUuid, sortSeqNo: index })"
                                        alt=""
                                    />
                                </div>
                                <dl class="dsc">
                                    <dt>{{ slide.clubNm }}</dt>
                                    <dd>
                                        <VIcon class="mr-10" icon="ic_location_green" :style="{ color: '#fff' }" />{{
                                            slide.clubPostAddr
                                        }}
                                    </dd>
                                    <dd>
                                        <span class="telIcon mr-10"></span>{{ slide.rpsTelno }}
                                        <VIcon icon="" :style="{ color: '#000', 'font-size': '10px' }" />
                                    </dd>
                                </dl>
                            </slide>
                        </carousel>
                        <a :href="`https://sportsclub-dev.sports.or.kr/club/${regionUrl}`" class="goHmBtn"
                            >클럽 <strong>홈페이지</strong></a
                        >
                    </div>
                    <div class="slide-foot mt-28 d-flex ai-center">
                        <button @click="myCarousel.prev" class="mr-10">
                            <VIcon icon="btn_pre" :style="{ 'font-size': '20px' }" />
                        </button>
                        <span class="bold">{{ myCarousel?.data.currentSlide.value + 1 }}</span>
                        <span class="gray">&nbsp; / &nbsp;{{ myCarousel?.data.slidesCount }}</span>
                        <button @click="myCarousel.next" class="ml-10">
                            <VIcon icon="btn_pre" :style="{ transform: 'rotate(180deg)', 'font-size': '20px' }" />
                        </button>

                        <button class="playBtn" @click="playState = !playState" :class="{ pause: !playState }">
                            <VIcon
                                class="ml-12"
                                :icon="playState ? 'btn_pause' : ''"
                                :style="{ color: '#222', 'font-size': '20px' }"
                            />
                        </button>
                    </div>
                </div>
                <ul class="l-citysBtns">
                    <li
                        v-for="(city, index) in cities"
                        :key="index"
                        :class="{ 'is-active': chosenCity?.siDoCd === city.siDoCd }"
                        @click="chosenCity = city"
                    >
                        {{ city.siDoNm }}
                    </li>
                </ul>
            </div>
        </div>
        <div class="r" :style="{ 'background-position': `-${mainMapPos} 0` }"></div>
    </div>
</template>

