import ajax from "@/util/ajax";
import axios, { AxiosRequestConfig, CancelTokenSource } from "axios";
import { MaybeRefOrGetter, ref, toValue, watch, Ref } from "vue";
import { useRouter } from "vue-router";

type RefOrGetter<T = any> = (() => T) | Ref<T>;

type FetchOptionParam<T> = {
    name: string;
    method: "get" | "post" | "delete" | "patch";
    param: MaybeRefOrGetter;
    config: AxiosRequestConfig;
    onSuccess: (data: T) => void;
    beforeFetch: () => void;
    onError: (error: any) => void;
    onFinally: () => void;
    enable: RefOrGetter<boolean>;
    dependancy: RefOrGetter[];
    query: boolean;
    baseURL: string;
};

type FetchParmas<T> = Partial<FetchOptionParam<T>> & {
    url: string | (() => string);
};

function useFetch<T>({
    url,
    method = "get",
    param,
    config = {},
    beforeFetch,
    onSuccess,
    onError,
    onFinally,
    dependancy = [],
    enable = () => true,
    name = "통신",
    baseURL,
    query
}: FetchParmas<T>) {
    const data = ref<T | null>(null);
    const loading = ref(false);
    const router = useRouter();

    // 취소를 위한 token source 선언
    let cancelSource: CancelTokenSource;

    const fetchData = async () => {
        beforeFetch && beforeFetch();
        loading.value = true;

        // 기존 요청이 존재하면 그 요청을 취소합니다.
        if (cancelSource) {
            cancelSource.cancel("기존 요청을 취소합니다.");
        }

        // 새로운 취소 토큰을 생성합니다.
        cancelSource = axios.CancelToken.source();

        let calcUrl: string = toValue(url);

        try {
            const res = await ajax(calcUrl, {
                method,
                params: toValue(param),
                cancelToken: cancelSource.token,
                ...config,
                ...(config.baseURL ? { baseURL: process.env.VUE_APP_URL + config.baseURL } : {}),
                ...(baseURL ? { baseURL: process.env.VUE_APP_URL + baseURL } : {})
            });

            data.value = res.data;
            loading.value = false;
            onSuccess && onSuccess(res.data);

            // 조회 영역 쿼리 스트링
            if (query && param) {
                router.push({ query: toValue(param) });
            }
        } catch (err) {
            onError && onError(err);

            if (axios.isCancel(err)) {
                console.log("이전 요청 취소", err.message);
            } else {
                loading.value = false;
                console.warn(name + " / " + err);
            }
        } finally {
            onFinally && onFinally();
        }
    };

    watch(
        [...dependancy, enable],
        () => {
            if (toValue(enable)) {
                fetchData();
            }
        },
        { immediate: true }
    );

    return { fetchData, data, loading };
}

export default useFetch;
