import axios from "axios";

const baseURL = `https://sportsclub-api-dev.sports.or.kr/common/file/`;

/**
 * 파일 리스트 파라미터
 */
type FileList = {
    fileUuid: string; //UUID
    sortSeqNo?: string; // 정렬번호
    pageNo?: number; // 페이지번호
    imgYn?: "Y" | "N"; // 이지미여부
    thumbUsagYn?: "Y" | "N"; // 썸네일여부
};

type FileInit = {
    fileUuid: string; //UUID
    sortSeqNo?: string; // 정렬번호
};

/**
 * 파일 정보
 */
export type FileInfo = {
    fileUuid: string; // 파일UUID
    sortSeqNo: string; // 정렬순서번호
    fileTpVl: string; // 파일유형값
    fileLnkTpCd: string; // 파일연결유형코드
    actlFileNm: string; // 실제파일명
    fileNm: string; // 파일명
    fileSize: number; // 파일사이즈
    filePth: string; // 파일경로
    fileExtsNm: string; // 파일확장자명
    usagYn: string; // 사용여부
    imgYn: string; // 이미지여부
    thumbWdthSize: number; // 썸네일가로사이즈
    thumbVelSize: number; // 썸네일세로사이즈
    thumbUsagYn: string; // 썸네일사용여부
    delYn: string; // 삭제여부
    frstRegDttm: string; // 최초등록일시
    frstRegpMemNo: number; // 최초등록자회원번호
    lstUpdDttm: string; // 최종수정일시
    lstAmdrMemNo: number; // 최종수정자회원번호
};

/**
 * 파일 업로드 컴포저블
 * @description
 * fileDelete : 파일 삭제 함수
 *
 * fileList : 파일 리스트형태로 가져오는 함수
 *
 * fileImage : 이미지 URL로 가져오는 함수
 */
function useFileUpload() {
    /**
     * 파일 삭제 API
     * @property fileUuid UUID*
     * @property sortSeqNo UUID에 해당 파일 순서
     */
    async function fileDelete(data: FileInit): Promise<boolean> {
        const res = await axios.delete(baseURL + data.fileUuid, { params: { sortSeqNo: data.sortSeqNo } });

        return res.data === 1;
    }

    /**
     * 파일 리스트 파일 정보를 배열로 반환한다.
     * @property fileUuid UUID*
     * @property sortSeqNo UUID에 해당 파일 순서
     * @property pageNo  페이지번호
     * @property imgYn 이지미여부
     * @property thumbUsagYn 썸네일여부
     */
    async function fileList(data: FileList): Promise<FileInfo[]> {
        const { fileUuid, ...param } = data;
        const res = await axios.get(baseURL + data.fileUuid, {
            params: param
        });

        return res.data.list as FileInfo[];
    }

    /**
     * 이미지파일 호출
     * @property fileUuid string UUID*
     * @property sortSeqNo  UUID에 해당 파일 순서
     * @property thumbUsagYn 썸네일여부
     * @property fileLnkTpCd 파일구분자
     */
    function fileImage(data: { fileUuid: string; sortSeqNo: number; thumbUsagYn?: "Y" | "N"; fileLnkTpCd?: string }) {
        return (
            baseURL +
            "image/" +
            data.fileUuid +
            `?sortSeqNo=${data.sortSeqNo ?? ""}&thumbUsagYn=${data.thumbUsagYn ?? ""}&fileLnkTpCd=${
                data.fileLnkTpCd ?? ""
            }`
        );
    }

    /**
     * 파일 다운로드 함수이다. 다중파일일때는 zip로 다운된다.
     * @property fileUuid UUID*
     * @property sortSeqNo UUID에 해당 파일 순서
     */
    async function fileDownload(data: FileInit) {
        try {
            await axios.get(baseURL + "download/" + data.fileUuid, {
                params: {
                    sortSeqNo: data.sortSeqNo ?? ""
                }
            });

            window.location.href =
                baseURL + "download/" + data.fileUuid + (data.sortSeqNo ? `?sortSeqNo=${data.sortSeqNo}` : "");
        } catch (err) {
            alert("파일이 없습니다." + `(${data.fileUuid})`);
        }
    }

    /**
     * 파일을 복사한다.
     * @property fileUuid UUID*
     * @property sortSeqNo UUID에 해당 파일 순서
     * @returns UUID
     */
    async function fileCopy(data: FileInit): Promise<string> {
        const res = await axios.get(baseURL + "copy/" + data.fileUuid, {
            params: {
                sortSeqNo: data.sortSeqNo ?? ""
            }
        });

        return res.data as string;
    }

    /**
     * 다중 파일 업로드
     * @description
     * UUID 있을때 : 기존 UUID에 파일 추가
     *
     * UUID 없을때 : 신규업로드 UUID 생성
     * @property  fileUuid 파일 UUID(필수)
     * @property  fileLnkTpCd 사용자 정의 구분자 (디폴드 : public)
     * @property  thumbUsagYn 썸네일 여부 (디폴드 : N)
     * @property  thumbWdthSize 썸네일 가로 사이즈 (디폴드 : 200)
     * @property  thumbVelSize 썸네일 세로 사이즈 (디폴드 : 100)
     */
    async function fileUpload(data: {
        files: File[];
        fileUuid?: string;
        fileLnkTpCd?: string;
        thumbUsagYn?: string;
        thumbWdthSize?: string;
        thumbVelSize?: string;
    }): Promise<string> {
        const formdata = new FormData();

        for (const key in data) {
            if (key !== "files") {
                const ele = data as any;
                if (ele[key]) {
                    formdata.append(key, ele[key]);
                }
            }
        }

        data.files.forEach(file => {
            formdata.append("files", file);
        });
        const res = await axios.post(baseURL + "/upload/", formdata);

        return res.data;
    }

    /**
     * 단일 파일 업로드
     * @description
     * UUID 있을때 : 기존 UUID 삭제 후 업로드
     *
     * UUID 없을때 : 신규 업로드
     * @property  fileUuid 파일 UUID(필수)
     * @property  fileLnkTpCd 사용자 정의 구분자 (디폴드 : public)
     * @property  thumbUsagYn 썸네일 여부 (디폴드 : N)
     * @property  thumbWdthSize 썸네일 가로 사이즈 (디폴드 : 200)
     * @property  thumbVelSize 썸네일 세로 사이즈 (디폴드 : 100)
     */
    async function fileSingleUpload(data: {
        files: File;
        fileUuid?: string;
        fileLnkTpCd?: string;
        thumbUsagYn?: string;
        thumbWdthSize?: string;
        thumbVelSize?: string;
    }): Promise<string> {
        const formdata = new FormData();
        for (const key in data) {
            if (key !== "files") {
                const ele = data as any;
                if (ele[key]) {
                    formdata.append(key, ele[key]);
                }
            }
        }
        formdata.append("files", data.files);

        const res = await axios.post(baseURL + "single/upload/", formdata);

        return res.data;
    }

    return { fileList, fileDelete, fileDownload, fileCopy, fileImage, fileSingleUpload, fileUpload };
}

export default useFileUpload;
