import "./style/index.scss";
import "@/icon-font/icon-font.css";
import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import { createPinia } from "pinia";
import globalVariable from "./plugins/global_variable";
import globalComponents from "./plugins/global_components";
import NotFound from "./pages/NotFound.vue";
import axios from "axios";

const pinia = createPinia();
const app = createApp(App);

app.directive("focus", {
    mounted: el => el.focus()
});

app.use(pinia);

app.use(globalVariable);
app.use(globalComponents);

type RoutePageInfo = {
    menuId: string;
    menuTrsCtet: string;
    menuUrl: string;
};

async function loadPage() {
    try {
        const res = await axios.get<RoutePageInfo[]>(
            process.env.VUE_APP_URL + "/common/member/router-page-url-list/PRTL"
        );

        for (const ele of res.data) {
            let path: string = ele.menuId;

            router.addRoute("home", {
                path,
                name: ele.menuId,
                component: () => import("@/pages/" + ele.menuUrl + ".vue").then(res => res).catch(() => NotFound)
            });
        }
    } catch (err) {
        alert("페이지 로드 에러");
    } finally {
        app.use(router);
        app.mount("#app");
    }
}

loadPage();
