<script setup lang="ts">
    import { Carousel, Slide } from "vue3-carousel";
    import { ref, computed } from "vue";
    import useFetch from "@/composables/useFetch";
    import useFileUpload from "@/composables/useFileUpload";
    const { fileImage } = useFileUpload();

    const galleryCarousel = ref<any>(null);

    const currentPage = computed(() => {
        if (galleryCarousel.value) {
            return Math.max(
                Math.min(
                    galleryCarousel.value.data.currentSlide.value + 1,
                    galleryCarousel.value.data.slidesCount.value
                ),
                1
            );
        }

        return 1;
    });

    const currentGage = computed(() => {
        if (galleryCarousel.value) {
            return (
                Math.max(
                    Math.min(
                        (galleryCarousel.value.data.currentSlide.value / galleryCarousel.value.data.slidesCount.value) *
                            100,
                        0
                    ),
                    100
                ) + "%"
            );
        }

        return "0%";
    });

    const { data } = useFetch<any>({
        url: "/po/community",
        baseURL: "/club",
        param: {
            bbsStgSqn: 543
        }
    });

    const galleryList = computed(() => data.value?.list);
</script>

<template>
    <section class="club-gallery">
        <div class="container">
            <header class="club-gallery-head" @click="console.log(galleryCarousel.data.currentSlide.value)">
                <div>
                    <h2 class="club-gallery-title">클럽 갤러리</h2>
                    <p class="club-gallery-info">클럽에서의 다양한 활동을 소개합니다</p>
                </div>
                <div class="d-flex ai-center">
                    <div class="line" v-if="galleryCarousel">
                        <span class="line-start">{{ currentPage }}</span>
                        <span class="line-end"> {{ galleryCarousel?.data.slidesCount.value }}</span>
                        <div class="line-child" :style="{ width: currentGage }"></div>
                    </div>
                    <button type="button" class="line-btn mr-10" @click="galleryCarousel.prev()">
                        <VIcon icon="ic_arrow_white" style="transform: rotate(180deg)" />
                    </button>
                    <button type="button" class="line-btn" @click="galleryCarousel.next()">
                        <VIcon icon="ic_arrow_white" />
                    </button>
                </div>
            </header>

            <div class="club-gallery-body">
                <Carousel
                    ref="galleryCarousel"
                    :items-to-show="5.2"
                    :snap-align="galleryList?.length <= 5 ? 'start' : 'end'"
                    :items-to-scroll="5"
                    :autoplay="galleryList?.length <= 5 ? undefined : 4000"
                    wrapAround
                    :mouseDrag="false"
                >
                    <Slide v-for="(slide, i) in galleryList" :key="i">
                        <div class="carousel__item gallery-item" @click="$window.alert('개발안함')">
                            <div class="gallery-img is-new">
                                <img :src="fileImage({ fileUuid: slide.fileUuid, sortSeqNo: 1 })" alt="" />
                            </div>
                            <h4>{{ slide.clubNm }}</h4>
                            <p>
                                {{ slide.ttlNm }}
                            </p>
                        </div>
                    </Slide>
                </Carousel>
            </div>
        </div>
    </section>
</template>

<style scoped lang="scss">
    %cornorBack {
        content: "";
        display: block;
        position: absolute;
        width: 300px;
        height: 300px;
        z-index: -1;
    }
    .club-gallery {
        border-radius: 210px 0 210px 0;
        position: relative;
        &::before {
            @extend %cornorBack;
            top: 0;
            left: 0;
            background: #f4f9fb;
        }
        &::after {
            @extend %cornorBack;
            right: 0;
            bottom: 0;
            background: #fff;
        }
    }
    .carousel__item {
        width: 100%;
        font-size: 20px;
        position: relative;
        overflow: hidden;
        margin-right: 30px;

        &.gallery-item {
            text-align: left;
            div {
                &.gallery-img {
                    position: relative;
                    &.is-new {
                        &::after {
                            content: "New";
                            position: absolute;
                            height: 28px;
                            width: 64px;
                            background-image: linear-gradient(to bottom, #f9cc22 0%, #ec8e1c);
                            right: 0;
                            bottom: -13px;
                            z-index: 1;
                            @include flex;
                            font-size: 17px;
                            border-bottom-left-radius: 20px;
                            border-bottom-right-radius: 20px;
                            border-top-left-radius: 20px;
                        }
                    }
                }
            }

            &-img {
                position: relative;
                display: none !important;
            }
            img {
                width: 100%;
                height: 160px;
                border-radius: 10px;
                object-fit: cover;
                display: block;
                margin-bottom: 28px;
            }

            h4 {
                font-weight: 500;
                font-size: 17px;
                margin-bottom: 10px;
            }

            p {
                word-break: keep-all;
                font-size: 15px;
                color: rgba(255, 255, 255, 0.7);
            }
        }
    }
    .line {
        width: 200px;
        height: 2px;
        background-color: rgba(255, 255, 255, 0.4);
        position: relative;
        margin-right: 50px;

        &-start {
            position: absolute;
            left: -25px;
            top: 50%;
            transform: translateY(-50%);
        }
        &-end {
            position: absolute;
            right: -25px;
            top: 50%;
            transform: translateY(-50%);
        }

        &-child {
            width: 20%;
            height: 100%;
            background-color: #fff;
            transition: all 0.2s;
        }
    }

    .line-btn {
        width: 44px;
        height: 44px;
        background-color: #fff;
        border-radius: 50%;
        @include flex;
        font-size: 25px;
    }
    .club-gallery {
        background: url("@/assets/imgs/bg_portal_gellary.jpg") no-repeat left top / 100%;
        min-height: 600px;
        /* background-position: bottom; */
        padding: 100px 0;
        color: #fff;

        &-head {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            margin-bottom: 60px;
        }

        &-info {
            font-size: 17px;
            letter-spacing: -0.51px;
            text-align: left;
            color: #fff;
        }

        &-title {
            font-size: 34px;
            font-weight: 700;
            color: #fff;
            margin-bottom: 18px;
        }
    }
</style>
