<script setup lang="ts">
    const tabFlag = ref(0);
    import "vue3-carousel/dist/carousel.css";
    import { Carousel, Slide, Pagination, Navigation } from "vue3-carousel";
    import { computed, ref, watch, watchEffect } from "vue";
    import useFetch from "@/composables/useFetch";

    const evfxCarousel = ref<any>(null);
    // 운영종목
    const evfxList = ref<OnMainEvfxResList[] | null>(null);
    const chosenEvfx = ref<OnMainEvfxResList | null>(null); //선택된 상위종목
    const evfxChildList = ref<OnMainEvfxResList[][] | null>(null);

    /// 상위운영종목 조회
    useFetch<MainTabCon02Type>({
        url: "/on-main/evfx",
        onSuccess: data => {
            const initOb = { grEvfxCd: "*", grEvfxNm: "전체", mchEvfxCd: null, mchEvfxNm: null };
            evfxList.value = [initOb, ...data.onMainEvfxResList];
            chosenEvfx.value = { ...initOb };
        }
    });
    /// 운영종목 목록
    useFetch<MainTabCon02Type>({
        url: () => `/on-main/evfx/{grEvfxCd}?grEvfxCd=${chosenEvfx.value?.grEvfxCd}`,
        enable: () => !!chosenEvfx.value,
        dependancy: [() => chosenEvfx.value],
        onSuccess: data => {
            const cloneData = [...data.onMainEvfxResList];

            const results = [];

            for (let i = 0; i < data.onMainEvfxResList.length; i += 12) {
                results.push(cloneData.slice(i, i + 12));
            }

            evfxChildList.value = results;
        }
    });

    const currentGage = computed(() => {
        if (evfxCarousel.value) {
            const currentIdx = evfxCarousel.value.data.currentSlide.value + 1;
            const totalCount = evfxCarousel.value.data.slidesCount.value;

            const pct = (currentIdx / totalCount) * 100;
            return pct + "%";
        }

        return "0%";
    });
</script>

<template>
    <div class="main-conts main-conts-evfx">
        <div class="evfx-top d-flex ai-center jc-between">
            <ul class="d-flex">
                <li
                    v-for="(evfx, index) in evfxList"
                    :key="index"
                    :class="{ 'is-active': evfx.grEvfxCd === chosenEvfx?.grEvfxCd }"
                    @click="chosenEvfx = evfx"
                >
                    {{ evfx.grEvfxNm }}
                </li>
            </ul>
            <div class="evfx-slide-idc d-flex ai-center">
                <div class="line" v-if="evfxCarousel">
                    <span class="line-start">{{
                        evfxCarousel.data.currentSlide.value + 1 < 10
                            ? `0${evfxCarousel.data.currentSlide.value + 1}`
                            : evfxCarousel.data.currentSlide.value + 1
                    }}</span>
                    <span class="line-end">
                        {{
                            evfxCarousel.data.slidesCount.value < 10
                                ? `0${evfxCarousel.data.slidesCount.value}`
                                : evfxCarousel.data.slidesCount.value
                        }}</span
                    >
                    <div class="line-child" :style="{ width: currentGage }"></div>
                </div>
                <button type="button" class="line-btn mr-10" @click="evfxCarousel.prev()">
                    <VIcon icon="ic_arrow_white" style="transform: rotate(180deg)" />
                </button>
                <button type="button" class="line-btn" @click="evfxCarousel.next()">
                    <VIcon icon="ic_arrow_white" />
                </button>
            </div>
        </div>
        <div class="evfx-slide">
            <Carousel ref="evfxCarousel" :items-to-show="1" v-if="evfxChildList" :mouse-drag="false">
                <Slide v-for="(slide, index) in evfxChildList" :key="index" class="evfxCarousel-slide">
                    <div class="evfx-item" v-for="(item, l) in slide" :key="l">
                        <div class="jongkmok d-flex ai-center jc-center">
                            <RouterLink to="/" class="hoverLink">{{ item.mchEvfxNm }}</RouterLink>
                            <img src="@/assets/imgs/img_tennis_active.png" alt="" />
                        </div>
                        <strong>{{ item.mchEvfxNm }}</strong>
                    </div>
                </Slide>
            </Carousel>
        </div>
        <img class="sportsClubText" src="@/assets/imgs/img_text_vertical.png" alt="" />
    </div>
</template>

<style scoped lang="scss">
    .main-conts-evfx {
        position: relative;
        .evfx-top {
            flex-wrap: wrap;
            li {
                padding: 12px 24px 10px;
                border-radius: 28px;
                background-color: #fff;
                color: #767676;
                @include font-style(16);
                margin-right: 12px;
                cursor: pointer;
                &.is-active {
                    background-color: #111;
                    color: #fff;
                    font-weight: 500;
                }
            }
        }
        .evfx-slide-idc {
            .line {
                width: 200px;
                height: 2px;
                background-color: #d9d9d9;
                position: relative;
                margin-right: 50px;
                span {
                    font-family: "Roboto", sans-serif;
                    @include font-style(16);
                    font-weight: 600;
                }
                &-start {
                    color: #111111;

                    position: absolute;
                    left: -30px;
                    top: 50%;
                    transform: translateY(-50%);
                }
                &-end {
                    position: absolute;
                    right: -30px;
                    top: 50%;
                    transform: translateY(-50%);
                    color: #767676;
                }

                &-child {
                    width: 20%;
                    height: 100%;
                    background-color: #333;
                    transition: all 0.2s;
                }
            }

            .line-btn {
                width: 44px;
                height: 44px;
                background-color: #fff;
                border-radius: 50%;
                @include flex;
                font-size: 25px;
                border: 1px solid #e1e1e1;
                i {
                    color: #333;
                }
            }
        }
        .evfx-slide {
            margin-top: 80px;
            min-height: 486px;
            width: 100%;
            .evfxCarousel-slide {
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
            }
            .evfx-item {
                margin-right: 60px;
                margin-bottom: 60px;
                flex-direction: column;
                align-items: center;
                cursor: pointer;
                .jongkmok {
                    width: 148px;
                    height: 148px;
                    background-color: #fff;
                    border-radius: 46px;
                    margin-bottom: 24px;
                    .hoverLink {
                        display: none;
                    }
                    &:hover {
                        box-shadow: 6px 6px 18px 0 rgba(0, 0, 0, 0.26);
                        position: relative;
                        overflow: hidden;
                        .hoverLink {
                            position: absolute;
                            display: block;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            background-image: linear-gradient(145deg, #f9cc22 13%, #ff6c3a 83%);
                            @include font-style(22);
                            font-weight: bold;
                            line-height: 148px;
                            text-align: center;
                            color: #fff;
                        }
                    }
                }
                .strong {
                    display: block;
                    text-align: center;
                    @include font-style(18);
                    font-weight: 500;
                }
            }
        }

        .sportsClubText {
            position: absolute;
            top: 194px;
            right: 0;
        }
    }
</style>
