<script setup lang="ts">
    import useFetch from "@/composables/useFetch";
    import { ref, computed } from "vue";

    const tabIdx = ref(0);

    const { data: data1 } = useFetch<any>({
        url: "/po/community",
        baseURL: "/club",
        param: {
            bbsStgSqn: 539
        },
        onSuccess(res) {
            console.log(res);
        }
    });

    const { data: data2 } = useFetch<any>({
        url: "/po/community",
        enable: () => tabIdx.value === 1,
        baseURL: "/club",
        param: {
            bbsStgSqn: 545
        }
    });

    const noticeList = computed(() => {
        let data: any;

        if (tabIdx.value === 0) {
            data = data1.value?.list;
        } else if (tabIdx.value === 1) {
            data = data2.value?.list;
        }

        if (!data) data = [];

        return data.slice(0, 5);
    });
</script>

<template>
    <section class="club-notice">
        <div class="container">
            <div class="club-notice-group">
                <div class="club-notice-area">
                    <div class="notice-header">
                        <h4>NOTICE</h4>
                        <ul>
                            <li :class="{ active: tabIdx === 0 }" @click="tabIdx = 0">클럽 새소식</li>
                            <li :class="{ active: tabIdx === 1 }" @click="tabIdx = 1">일자리 정보</li>
                        </ul>
                    </div>
                    <table class="notice-table">
                        <colgroup>
                            <col style="width: 25%" />
                            <col style="width: 70%" />
                            <col style="width: 10%" />
                        </colgroup>
                        <tbody>
                            <tr v-for="a in noticeList">
                                <td>
                                    <div class="club-name">{{ a.clubNm }}</div>
                                </td>
                                <td>
                                    <div class="club-info">{{ a.ttlNm }}</div>
                                </td>
                                <td class="club-date">{{ $dayjs(a.frstRegDttm).format("YYYY.MM.DD") }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="club-youtube-area">
                    <div class="youtube-header">
                        <h4>YOUTUBE</h4>
                        <a href="#">바로가기<VIcon icon="ic_arrow_white" /></a>
                    </div>
                    <div class="youyube-content">
                        <div class="youyube-thumb">
                            <img
                                src="https://marketplace.canva.com/EAFCpPXxAMU/2/0/1600w/canva-%EB%B2%A0%EC%9D%B4%EC%A7%80%EC%83%89-%EB%B0%9D%EC%9D%80-%EA%B0%88%EC%83%89-%ED%9D%B0%EC%83%89-%EB%B3%84%EB%82%9C-%EC%B0%BD%EC%9E%91%EC%9E%90-%EC%97%AC%ED%96%89-%EB%B8%8C%EC%9D%B4%EB%A1%9C%EA%B7%B8-%EC%9C%A0%ED%8A%9C%EB%B8%8C-%EC%8D%B8%EB%84%A4%EC%9D%BC-ZlatTGzb8-A.jpg"
                                alt=""
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

