<script setup lang="ts">
    const tabFlag = ref(0);
    import "vue3-carousel/dist/carousel.css";
    import { Carousel, Slide } from "vue3-carousel";
    import { computed, ref, watch, watchEffect } from "vue";
    import useFetch from "@/composables/useFetch";

    const apptClbCarousel = ref<any>(null);

    // 지정 스포츠클럽
    const clubList = ref<OnMainClubResList[][] | null>(null);
    const alphabetArray = ["ㄱ", "ㄴ", "ㄷ", "ㄹ", "ㅁ", "ㅂ", "ㅅ", "ㅇ", "ㅈ", "ㅊ", "ㅋ", "ㅌ", "ㅍ", "ㅎ", "A~Z"];

    /**
     * Y 스포츠클럽전체
     * 30 지정스포츠클럽
     * 31 예비지정스포츠클럽
     */
    const category = ref<"Y" | "30" | "31">("Y"); //선택된 카테고리
    const chosenInitial = ref<string>(alphabetArray[0]); // 초성선택

    /// 지역별 클럽 조회
    useFetch<MainTabCon01Type>({
        url: "/on-main",
        dependancy: [() => category.value, () => chosenInitial.value],
        param: () => ({
            apmtYn: category.value,
            initial: chosenInitial.value
        }),
        onSuccess: data => {
            console.log(data);

            const arr = [];
            // clubList.value = data.onMainClubResList;
            for (let i = 0; i < data.onMainClubResList.length; i += 36) {
                arr.push(data.onMainClubResList.slice(i, i + 36));
            }

            clubList.value = arr;

            console.log(arr);
        }
    });

    const currentGage = computed(() => {
        if (apptClbCarousel.value) {
            const currentIdx = apptClbCarousel.value.data.currentSlide.value + 1;
            const totalCount = apptClbCarousel.value.data.slidesCount.value;

            const pct = (currentIdx / totalCount) * 100;
            console.log(pct);
            return pct + "%";
        }

        return "0%";
    });
</script>

<template>
    <div class="main-conts main-conts-appintedClubs">
        <div class="d-flex categoryBtns">
            <button @click="category = 'Y'" :class="{ 'is-active': category === 'Y' }">전체<em>개</em></button>
            <button @click="category = '30'" :class="{ 'is-active': category === '30' }">
                지정스포츠클럽<em>개</em>
            </button>
            <button @click="category = '31'" :class="{ 'is-active': category === '31' }">
                예비 지정스포츠클럽<em>개</em>
            </button>
        </div>

        <div class="appt-top d-flex ai-center jc-between">
            <div class="d-flex">
                <button
                    class="initialBtn"
                    v-for="a in alphabetArray"
                    :class="{ 'is-active': chosenInitial === a, roboto: a === 'A~Z' }"
                    @click="chosenInitial = a"
                >
                    {{ a }}
                </button>
            </div>
            <div class="appt-slide-idc d-flex ai-center">
                <div class="line" v-if="apptClbCarousel">
                    <span class="line-start">{{
                        apptClbCarousel.data.currentSlide.value + 1 < 10
                            ? `0${apptClbCarousel.data.currentSlide.value + 1}`
                            : apptClbCarousel.data.currentSlide.value + 1
                    }}</span>
                    <span class="line-end">
                        {{
                            apptClbCarousel.data.slidesCount.value < 10
                                ? `0${apptClbCarousel.data.slidesCount.value}`
                                : apptClbCarousel.data.slidesCount.value
                        }}</span
                    >
                    <div class="line-child" :style="{ width: currentGage }"></div>
                </div>
                <button type="button" class="line-btn mr-10" @click="apptClbCarousel.prev()">
                    <VIcon icon="ic_arrow_white" style="transform: rotate(180deg)" />
                </button>
                <button type="button" class="line-btn" @click="apptClbCarousel.next()">
                    <VIcon icon="ic_arrow_white" />
                </button>
            </div>
        </div>
        <div class="appt-slide">
            <Carousel ref="apptClbCarousel" :items-to-show="1" v-if="clubList" :mouse-drag="false">
                <Slide v-for="(slide, index) in clubList" :key="index" class="slide-item">
                    <RouterLink
                        :to="`${$baseDo}/club/${item.rpsUrl}`"
                        class="club-item"
                        v-for="(item, l) in slide"
                        :key="l"
                    >
                        {{ item.clubNm }}
                        <VIcon
                            icon="ic_arrow"
                            :style="{ transform: 'rotate(-90deg)', color: '#333', 'font-size': '20px' }"
                        />
                    </RouterLink>
                </Slide>
            </Carousel>
        </div>
        <div class="sClbTxt">
            <img src="@/assets/imgs/img_text_horizen.png" alt="" />
        </div>
    </div>
</template>

